// 
// topbar.scss
//


.navbar-custom {
    padding: 0 22px 0 0;
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    height: $topbar-height;
    z-index: 100;
    background-color: $bg-topbar-dark;
    box-shadow: $box-shadow-sm;

    .navbar-brand {
        padding: 0 20px 0 30px;
        width: 250px;
        margin-right: 0;
        text-align: center;
        .logo-lg {
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .topnav-menu {
        >li {
            float: left;
        }

        .nav-link {
            padding: 0 15px;
            color: rgba($white, 0.6);
            min-width: 32px;
            display: block;
            line-height: $topbar-height;
            text-align: center;
            max-height: $topbar-height;

            svg {
                height: 20px;
                width: 20px;
                color: rgba($white, 0.6);
            }

            &:hover,
            &:focus,
            &:active {
                color: rgba($white, 0.6);

                svg {
                    color: rgba($white, 0.6);
                }
            }
        }
    }

    .dropdown.show {
        .nav-link {
            background-color: rgba($white, 0.05);
        }
    }

    .menu-left {
        overflow: hidden;
    }

    /* Search */
    .app-search {
        padding: 17px 0;
        overflow: hidden;
        margin-left: 15px;
        margin-right: 15px;

        form {
            max-width: 320px;
        }

        .form-control {
            border: 1px solid transparent;
            height: 38px;
            padding-right: 40px;
            padding-left: 20px;
            background-color: rgba($white, .07);
            box-shadow: none;
            border-radius: 50px;
            color: $white;
        }

        svg {
            font-size: 18px;
            color: rgba($white, .3);
            @include position(absolute, 50%, 15px, null, null);
            @include transform(translateY(-50%));
            width: 20px;
            height: 20px
        }
    }


    .button-menu-mobile {
        border: none;
        color: $gray-500;
        display: block;
        height: $topbar-height;
        line-height: $topbar-height;
        width: 60px;
        background-color: transparent;
        font-size: 24px;
        cursor: pointer;

        &:focus {
            outline: none;
        }

        &.disable-btn {
            display: none;
        }

        .menu-icon {
            display: inline-block;
        }

        .close-icon {
            display: none;
        }

        &.open {
            .menu-icon {
                display: none;
            }

            .close-icon {
                display: inline-block;
            }
        }
    }

    .fullscreen-toggle {
        .feather-minimize {
            display: none;
        }

        &.minimize-screen {
            .feather-maximize {
                display: none;
            }

            .feather-minimize {
                display: inline-block;
            }
        }
    }

    // Logo
    .logo {
        // width: $leftbar-width;

        span.logo-sm {
            display: none;
        }
    }
    .logo-dark {
        display: none;
    }
}
.text-logo {
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 1px;
    color: rgba($white, 0.6);
}

.logo-dark {
    color: rgba($bg-leftbar-dark, 0.8);
    .text-logo { 
        color: rgba($bg-leftbar-dark, 0.8);
    }
}
/* Notification */
@keyframes blinker {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

.noti-scroll {
    max-height: 230px;
}

.notification-list {
    margin-left: 0;

    .noti-title {
        background-color: transparent;
        padding: 7px 20px 15px;
    }

    .noti-icon {
        font-size: 21px;
        vertical-align: middle;
    }

    .noti-icon-badge {
        display: inline-block;
        position: absolute;
        top: 24px;
        right: 16px;
        width: 8px;
        height: 8px;
        background-color: $danger;
        border: 2px solid transparent;
        border-radius: 50%;
        box-shadow: $box-shadow-lg;
        animation: blinker 3s linear infinite;
        animation-fill-mode: initial;
    }

    .notify-item {
        padding: 12px 20px;

        .notify-icon {
            float: left;
            height: 36px;
            width: 36px;
            font-size: 16px;
            line-height: 36px;
            text-align: center;
            margin-right: 10px;
            border-radius: 50%;
            color: $white;
        }

        .notify-details {
            margin-bottom: 5px;
            overflow: hidden;
            margin-left: 45px;
            text-overflow: ellipsis;
            white-space: nowrap;
            color: $gray-800;
            line-height: 1.5;

            b {
                font-weight: 500;
            }

            small {
                display: block;
            }

            span {
                display: block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 13px;
            }
        }

        .user-msg {
            margin-left: 45px;
            white-space: normal;
            line-height: 16px;
        }
    }

    .profile-dropdown-items {
        .notify-item {
            padding: $dropdown-item-padding-y $dropdown-item-padding-x;
        }
    }
}

.profile-dropdown {
    display: none;
}

.profile-dropdown-items {
    width: 170px;

    i {
        vertical-align: middle;
        margin-right: 5px;
    }
}

.nav-user {
    padding: 0 12px !important;

    img {
        height: 32px;
        width: 32px;
    }
}

/* =============
  Light topbar
============= */
.light-topbar {
    .navbar-custom {
        background: $bg-topbar-light;
        .logo {
            display: none;
        }
        .logo-dark {
            display: block;
            .logo-sm {
                display: none;
            }
        }
    }
    .navbar-custom {
        .button-menu-mobile {
            color: rgba($bg-leftbar-dark, 0.8);
        }
        .topnav-menu {
            .nav-link {
                color: rgba($bg-leftbar-dark, 0.8);
                &:focus, &:active, &:hover {
                    color: rgba($bg-leftbar-dark, 0.8);
                    svg {
                        color: rgba($bg-leftbar-dark, 0.8);
                    }
                }
                svg {
                    color: rgba($bg-leftbar-dark, 0.8);
                }
            }
        }
        .app-search {
            svg {
                color: rgba($bg-leftbar-dark, 0.8);
            }
            .form-control {
                border-color: rgba($bg-leftbar-dark, 0.8);
                color: rgba($bg-leftbar-dark, 0.8);
            }
        }
    }
}

@media (max-width: 1024px) {
    .navbar-custom {
        left: 0;
        padding: 0 10px;
        margin: 0px;

        .button-menu-mobile.disable-btn {
            display: block;

        }

        .logo {
            display: none;
        }
    }
}

@include media-breakpoint-up(lg) {
    .navbar-custom {
        .button-menu-mobile {
            margin-left: 4px;
        }
    }
}

@include media-breakpoint-down(md) {
    .navbar-custom {
        .app-search {
            .form-control {
                border-color: rgba($bg-topbar-light, 0.8);
            }

            svg {
                color: rgba($bg-topbar-light, 0.8);
            }
        }
    }

}

@include media-breakpoint-down(xs) {
    .navbar-custom {
        .dropdown {
            position: static;
            .dropdown-menu {
                left: 15px !important;
                right: 15px !important;
                width: calc(100% - 30px) !important;
            }
        }
        .notification-list {
            & > .dropdown-toggle {
                position: relative;
            }
        }
        .page-title {
            display: none;
        }
    }
}